import * as React from 'react'
import SwipeableViews from 'react-swipeable-views'
import styled from 'styled-components'
import { graphql, useStaticQuery } from 'gatsby'
import Layout from '../../Layout'
import Section from './Section'
import SectionTabs from './SectionTabs'

const GET_MENU = graphql`
  query {
    brewbox {
      menu {
        id, name, items
      }
    }
  }
`

const MenuContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

// Menu Feature initializer

export default () => {
  const [ index, setIndex ] = React.useState(0)
  const data = useStaticQuery(GET_MENU)
  const rootItems = data.brewbox.menu.items.objects

  const sections = React.useMemo(()=>rootItems.map((section, sIndex)=><Section section={section} key={sIndex}/>), [])
  //const sectionTabs = React.useMemo(()=>rootItems.map((section, sIndex)=><div key={sIndex}>{section.title}</div>), [index])

  return (
    <Layout>
      <MenuContainer>
        <SectionTabs sections={rootItems} index={index} onTabChange={setIndex}/>
        <SwipeableViews style={{flex: 1}} index={index} onChangeIndex={i=>setIndex(i)}>
          {sections}
        </SwipeableViews>
      </MenuContainer>
    </Layout>
  )
}