export enum MenuObjectType {
  Section, Category, Item
}

export type MenuObjectArray = FullMenuObject[]

export interface MenuObject {
  __type: MenuObjectType;
  title?: string;
  objects: MenuObjectArray;
}

export interface SectionMenuObject extends MenuObject {
  __type: MenuObjectType.Section;
  title: string;
}

export interface CategoryMenuObject extends MenuObject {
  __type: MenuObjectType.Category;
  title: string;
}

export type Currency = 'USD'|'CAD'|'EUR'

export interface ItemVariation {
  /** Name of variations example: small/medium/large or 8oz/12oz/16oz */
  name: string;
  /** SKU for given variation */
  sku: string;
  /** Cost for given variation in cents for USD and equivalent for all others */
  amount: number;
  currency: Currency;
}

export interface ItemMenuObject extends MenuObject {
  __type: MenuObjectType.Item;
  /** If no providerId, item exists server side and uid = id */
  providerId?: number;
  uid: string;
  imageUrl?: string;
  variations: ItemVariation[]
}

export type FullMenuObject = SectionMenuObject|
                              CategoryMenuObject|
                              ItemMenuObject

export interface ItemMenuObjectInit extends Partial<ItemMenuObject> {
  __type: MenuObjectType.Item;
  objects: MenuObjectArray;
}