import * as React from 'react'
import styled from 'styled-components'
import { SectionMenuObject } from '../../../types/menu_object'

const SectionTabsContainer = styled.div`
  display: flex;
  position: sticky;
  top: 0;
  height: 60px;
  background-color: #FFFBE6;
  z-index: 10;
`

const SectionTabContainer = styled.div<Partial<SectionTabProps>>`
  flex: 1;
  color: #29302E;
  border-bottom: solid 2px #29302E;
  opacity: .32;
  display: flex;
  justify-content: center;
  align-items: center;
  ${props => props.active && `
    opacity: 1;
    border-bottom: solid 2px #FD5523;
    color: #FD5523
  `}
`

const SectionTitle = styled.div<Partial<SectionTabProps>>`
  font-family: 'bebas neue';
`

interface SectionTabProps {
  section: SectionMenuObject;
  active: boolean;
  onTabClick():void;
}

const SectionTab = ({section, active, onTabClick}:SectionTabProps) => {
  return (
    <SectionTabContainer active={active} onClick={onTabClick}>
      <SectionTitle>
        {section.title}
      </SectionTitle>
    </SectionTabContainer>
  )
}

export interface SectionTabsProps {
  sections: SectionMenuObject[];
  index: number;
  onTabChange(index: number):void;
}

export default ({index, sections, onTabChange}:SectionTabsProps) => {
  const sectionTabs = React.useMemo(()=>sections.map((section, sIndex)=><SectionTab key={sIndex} section={section} active={sIndex===index} onTabClick={()=>onTabChange(sIndex)}/>), [index])
  return (
    <SectionTabsContainer>
      {sectionTabs}
    </SectionTabsContainer>
  )
}