import * as React from 'react'
import styled from 'styled-components'
import { SectionMenuObject, MenuObjectType, FullMenuObject } from '../../../types/menu_object'
import { Category } from './Category'
import { Item } from './Item'

const menuObjectReducer = (menuObject: FullMenuObject) => {
  switch(menuObject.__type) {
    case MenuObjectType.Category:
      return <Category category={menuObject}/>
    case MenuObjectType.Item:
      return <Item item={menuObject}/>
  }
}

interface MenuObjectItemProps {
  object: FullMenuObject;
}

const MenuObjectItem = ({object}:MenuObjectItemProps) => {
  const { objects } = object
  const items = React.useMemo(()=>objects.map((item, index)=><MenuObjectItem object={item} key={index}/>), [objects])
  return (
    <>
      { menuObjectReducer(object) }
      { items }
    </>
  )
}

const SwipeContainer = styled.div`
  padding: 5px 15px 0 15px;
`

export interface SectionProps {
  section: SectionMenuObject;
}

export default ({section}:SectionProps) => {
  const items = React.useMemo(() => {
    return section.objects.map((item, index) => <MenuObjectItem object={item} key={index}/>)  
  }, [section])
  return (
    <SwipeContainer>
      {items}
    </SwipeContainer>
  )
}