import * as React from 'react'
import styled from 'styled-components'
import { ItemMenuObject, Currency } from '../../../types/menu_object'

const ItemContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  
`

const ItemTitle = styled.div`
  font-weight: 800;
`

const ItemPrice = styled.div``

const ItemLine = styled.div`
  flex: 1;
  margin: 0 4px 4px 4px;
  border-bottom: dotted 2px rgba(0, 0, 0, .3);
`

const reduceCurrency = (currency: Currency) => {
  switch(currency){
    case 'USD': case 'CAD': return '$'
    case 'EUR': return 'â‚¬'
  }
  return '$'
}

export interface ItemProps {
  item: ItemMenuObject;
}

export const Item = ({item}:ItemProps) => {

  const price = React.useMemo(()=>{
    if(item.variations.length === 0){return 'N/A'}
    return <ItemPrice>{reduceCurrency(item.variations[0].currency)} {item.variations[0].amount / 100}</ItemPrice>
  }, [item])

  return (
    <ItemContainer>
      <ItemTitle>{item.title}</ItemTitle>
      <ItemLine/>
      {price}
    </ItemContainer>
  )
}